import { Component } from "react";
import Page from './Page.js';
import Center from './Center.js';
import Card from './Card.js';
import { Suspense } from "react";
import { ThreeCanvas, Mike } from './ThreeCanvas.js';

export default class PageContact extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight
        }

        this.Resize = this.Resize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.Resize, false);
    }

    Resize() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight
        });
    }

    render() {
        return (
            <Page color="#FFC1A7">
                <Center>
                    <Card color="#C0917E">
                        <div className="work-title">CONTACT ME</div>
                        <div className="work-type" style={{ marginBottom: "20px" }}><a href="https://www.linkedin.com/in/federico-garcia-garcia/" target="_blank">LinkedIn</a></div>
                    </Card>
                </Center>
            </Page>
        );
    }
}