import './App.css';
import { Component } from "react";
import PageWelcome from './PageWelcome.js';
import PageSkills from './PageSkills.js';
import PageContact from './PageContact.js';
import PageExperience from './PageExperience.js';
import Background from './Background.js';
import BackgroundGradient from './BackgroundGradient.js';
import { Suspense } from "react";
import { ThreeCanvas, Stan, Alien } from './ThreeCanvas.js';

export default class App extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            showScrollForMore: false
        }

        this.OnClick = this.OnClick.bind(this);
        this.OnScroll = this.OnScroll.bind(this);

        this.timeout = 0;
    }

    OnClick() {
        this.refs.stan.OnClick();
    }

    OnScroll() {
        if(this.timeout) {
            this.timeout = 0;
            clearTimeout(this.timeout);
        }

        this.setState({
            showScrollForMore: false
        });
    }

    componentDidMount() {
        this.timeout = setTimeout(function() {
            this.setState({
                showScrollForMore: true
            });
        }.bind(this), 1);
    }

    render() {
        return (
            <div className='App'>
                <div id='App-scroll' onScroll={this.OnScroll}>
                    <BackgroundGradient />
                    <PageWelcome />
                    <PageSkills />
                    <PageExperience />
                    <PageContact />
                    {/* <ThreeCanvas onClick={this.OnClick} position="right">
                    <ambientLight />
                    <pointLight position={[0, 0, 10]} />
                    <Suspense>
                        <Stan ref="stan" />
                    </Suspense>
                </ThreeCanvas> */}
                </div>

                <div
                    className='scroll-for-more'
                    style={{
                        opacity: this.state.showScrollForMore ? "1" : "0",
                        transitionDelay: this.state.showScrollForMore ? "4s" : "0s"
                        }}
                    >
                    Scroll for more
                    <br/>
                    &#9660;
                </div>
            </div>
        );
    }
}