import { Component } from "react";
import background from './background.png'
import moon2 from './moon2.svg'
import back from './back.svg'
import middle from './middle.svg'
import top from './top.svg'

export default class Background extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight,
            scroll: 0,
        }

        this.Scroll = this.Scroll.bind(this);
    }

    componentDidMount() {
        document.getElementById("app-scroll").addEventListener('scroll', this.Scroll, false);
    }

    Scroll() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight,
            scroll: document.getElementById("app-scroll").scrollTop,
        });
    }

    render() {
        return (
            <div
                style={{
                    position: "fixed", top: "0", left: "0",
                    background: `url(${background})`, backgroundSize: "cover", backgroundPosition: "10% 10%",
                    width: "100%", height: "100vh",
                    // transform: `scale(1.25) translateY(${-(this.state.scroll / this.state.h) * 3}%)`,
                    // transition: this.state.w/this.state.h < 1.333 ? "none" : "transform 0.3s ease-out"
                }}
            >
                    {/* <Element src={back} className="" size="30%" x="8%" y="50%"/>
                    <Element src={middle} className="moon" size="29%" x="8%" y="50%"/>
                    <Element src={top} className="moon" size="30%" x="8%" y="50%"/> */}
            </div>
        );
    }
}

class Element extends Component {
    constructor(props) {
        super(props);

        this.props = props;
    }

    render() {
        return (
            <div style={{
                position: "absolute", top: this.props.y, left: this.props.x,
                width: this.props.size, height: this.props.size, transform: "translate(-50%, -50%)"}}>
            <img
                src={this.props.src}
                className={this.props.className}
                style={{
                    width: "100%", height: "100%",
                }}
            />
            </div>
        );
    }
}