import { Component } from "react";
import Page from './Page.js';
import Center from './Center.js';
import Card from './Card.js';
import Skill from './Skill.js';
import Skills from './Skills.js';
import SkillsColumn from './SkillsColumn.js';
import { Suspense } from "react";
import { ThreeCanvas, Leela } from './ThreeCanvas.js';
import Experience from "./Experience.js";

export default class PageExperience extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight
        }

        this.Resize = this.Resize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.Resize, false);
    }

    Resize() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight
        });
    }

    render() {
        return (
            <Page color="#6CC2BD">
                <Center>
                    <Card color="#51928E">
                        <div className="work-title">EXPERIENCE</div>
                        <Skills>
                            <SkillsColumn>
                                <Experience year="2020" experience="Frontend Developer" place="Skeleton Crew Studio" link="https://www.skeletoncrew.co.jp/" />
                                <Experience year="2019" experience="Bachelor Degree in Computer Science" place="University of Granada" link="https://www.ugr.es/en" />
                                <Experience year="2018" experience="1 year Front Developer Internship" place="University of Granada" link="https://www.ugr.es/en" />
                            </SkillsColumn>
                        </Skills>
                    </Card>
                </Center>
            </Page>
        );
    }
}

