import { Component } from "react";

export default class Page extends Component {
    constructor(props) {
        super(props);

        this.props = props;
        this.w = 720;
        this.h = 1080;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight,
            scale: Math.min(window.innerWidth / this.w, window.innerHeight / this.h)
        }

        this.Resize = this.Resize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.Resize, false);
    }

    Resize() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight,
            scale: Math.min(window.innerWidth / this.w, window.innerHeight / this.h)
        });
    }

    render() {
        return (
            <div
                className="page" id={this.props.id}
            >
                <div
                    className="page-child"
                    style={{
                        transformOrigin: "center",
                        transform: `translate(${(this.state.w - 608) / 2}px, ${(this.state.h - this.h) / 2}px) scale(${this.state.scale})`
                    }}
                >
                    {this.props.children}
                </div>
            </div>
        );
    }
}