import { Component } from "react";

export default class Experience extends Component {
    constructor(props) {
        super(props);

        this.props = props;
    }

    render() {
        return (
            <div className="skill">
                <table>
                    <tr style={{verticalAlign: "top"}}>
                        <td>
                            <div style={{whiteSpace: "pre"}}>{this.props.year} - </div>
                        </td>
                        <td>
                            <div style={{paddingBottom: "0.4rem"}}>{this.props.experience} </div>
                            <div>@&#8201;<a href={this.props.link} target="_blank">{this.props.place}</a></div>
                        </td>
                    </tr>
                </table>
            </div>
        );
    }
}