import { Component } from "react";
import background from './background.png'
import backgroundSquares from './background-squares.svg'
import backgroundAnimated from './background-animated.svg'
import moon2 from './moon2.svg'
import back from './back.svg'
import middle from './middle.svg'
import top from './top.svg'

export default class BackgroundGradient extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.body = document.body;
        this.html = document.documentElement;

        this.hue = 0;

        this.state = {
            hue: this.hue,
        }

        this.Update = this.Update.bind(this);
        this.GetHue = this.GetHue.bind(this);
    }

    componentDidMount() {
        this.appScroll = document.getElementById('App-scroll');

        requestAnimationFrame(this.Update);
    }

    GetHue() {
        return this.appScroll.scrollTop / this.appScroll.scrollHeight;
    }

    Update() {
        this.hue = this.hue * 0.9 + this.GetHue() * 0.1;

        this.setState({
            hue: this.hue
        });

        requestAnimationFrame(this.Update);
    }

    render() {
        return (
            <>
                <div
                    className="background-gradient"
                >
                </div>
                <div
                    className="background-gradient fade-in"
                    style={{
                        filter: `hue-rotate(${(this.state.hue) * 270}deg)`
                    }}
                >
                </div>
                <img className="background-animated" src={backgroundAnimated} style={{ transform: "translate(10%, -10%)"}}/>
                <img className="background-animated" src={backgroundAnimated} style={{ transform: "translate(-10%, 10%)rotate(180deg)" }}/>
            </>
        );
    }
}