import { Component } from "react";
import Page from './Page.js';
import Center from './Center.js';
import Card from './Card.js';

export default class PageWelcome extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight
        }

        this.Resize = this.Resize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.Resize, false);
    }

    Resize() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight
        });
    }

    render() {
        return (
            <Page id="page-welcome" color="#F67E7D">
                <Center>
                    <Card color="#B95F5E">
                        <h1>FEDERICO<br />GARCIA</h1>
                        <div className="work-title">FRONTEND DEVELOPER</div>
                        <div className="work-type">Making apps, websites,<br />AR content, and more</div>
                    </Card>
                </Center>
            </Page>
        );
    }
}