import { Component } from "react";
import Page from './Page.js';
import Center from './Center.js';
import Card from './Card.js';
import Skill from './Skill.js';
import Skills from './Skills.js';
import SkillsColumn from './SkillsColumn.js';
import { Suspense } from "react";
import { ThreeCanvas, George } from './ThreeCanvas.js';

export default class PageSkills extends Component {
    constructor(props) {
        super(props);

        this.props = props;

        this.state = {
            w: window.innerWidth,
            h: window.innerHeight
        }

        this.Resize = this.Resize.bind(this);
    }

    componentDidMount() {
        window.addEventListener('resize', this.Resize, false);
    }

    Resize() {
        this.setState({
            w: window.innerWidth,
            h: window.innerHeight
        });
    }

    render() {
        return (
            <Page id="page-skills" color="#7D7AA2">
                <Center>
                    <Card color="#5E5C7A">
                        <div className="work-title">SKILLS</div>
                        <Skills>
                            <SkillsColumn>
                                <Skill>HTML</Skill>
                                <Skill>JavaScript</Skill>
                                <Skill>Bootstrap</Skill>
                                <Skill>Three.js</Skill>
                                <Skill>UE4</Skill>
                                <Skill>C#</Skill>
                                <Skill>Java</Skill>
                                <Skill>Git</Skill>
                            </SkillsColumn>
                            <SkillsColumn>
                                <Skill>CSS</Skill>
                                <Skill>React</Skill>
                                <Skill>ASP.NET</Skill>
                                <Skill>Unity</Skill>
                                <Skill>Python</Skill>
                                <Skill>C++</Skill>
                                <Skill>8th Wall</Skill>
                                <Skill>AWS</Skill>
                            </SkillsColumn>
                        </Skills>
                    </Card>
                </Center>
            </Page>
        );
    }
}
